import { SketchPicker, CustomPicker } from "react-color";
import { useState } from "react";
import { ColorList } from "./ColorList";

function ColorPanel ({element}) {
	const [elementColor, setElementColor] = useState(element.color)

	const emptyColors = []
	for (let i = 0; i<16; i++) emptyColors.push(`rgba(255, 255, 255, ${i*100})`)
	const [recentColors, setrecentColors] = useState(emptyColors)

	const [favoiteColors, setFavoiteColors] = useState([])

	const defaultColors = [
		'rgb(255, 180, 92)',
		'rgb(63, 169, 245)',
		'rgb(159, 212, 250)',
		'rgb(185, 0, 0)',
		'rgb(102, 225, 51)',
		'rgb(0, 0, 0)',
		'rgb(230, 230, 230)',
		'rgb(255, 255, 255)',
	]

	const changeColor = (color) => {
		element.color = color

		setElementColor(color)

		if(!element.e) return

		if(element.e.style.stroke) {
			element.e.style.stroke = color
		}
		if(element.e.style.fill && element.e.style.fill !== "none") {
			element.e.style.fill = color 
		}
	}

	const getColor = (e) => {
		if(e.target.className === 'color-block'){
			if(e.target.title === 'addColor'){
				if(favoiteColors.indexOf(element.color) !== -1) return
				setFavoiteColors([...favoiteColors, element.color])
				return
			}
			if(e.target.title === 'removeColor'){
				if(favoiteColors.indexOf(element.color) === -1) return
				const newFavoriteColors = [...favoiteColors]
				newFavoriteColors.splice(favoiteColors.indexOf(element.color),1)
				setFavoiteColors(newFavoriteColors)
				return
			}

			changeColor(e.target.style.background)
		}
	}

	return (
		<div className="ColorPanel" onClick={(e) => getColor(e)}>
			<a>{"Element: " + element.id}</a>
			<a>{"Color: " + element.color }</a>
			
			<div className="ColorPicker" style = {{width: '105%'}}>
				<SketchPicker
					width = {'100%'}
					disableAlpha={true}
					presetColors={[]}
					color={element.color}
					onChange={(color) => {
						changeColor(`rgb(${color.rgb.r}, ${color.rgb.g}, ${color.rgb.b})`)
					}}
					onChangeComplete={(color) => {
						const rgb = `rgb(${color.rgb.r}, ${color.rgb.g}, ${color.rgb.b})`
						if(defaultColors.concat(recentColors).indexOf(rgb) === -1){
							const preset = [rgb].concat(recentColors)
							if(recentColors.length >= 16) preset.pop()
							setrecentColors(preset)
						}
					}}
				/>
				<ColorList colors={defaultColors}/>
				<ColorList colors={recentColors}/>
				<ColorList colors={favoiteColors} button='+'/>
			</div>
		</div>
	)
}

export {ColorPanel}