import './App.css';
import { ReactComponent as Interface } from './interface2.svg';
import { ColorPanel } from './components/ColorPanel';
import { useState } from 'react';

function App() {

	const [element, setElement] = useState({id: "None"})

	function getElement (e) {
		console.log(e.target.parentElement)
		const style = e.target.parentElement.style

		const newElement = {
			e: e.target.parentElement,
			id: e.target.parentElement.id,
			color: style.stroke ? style.stroke : style.fill
		}
		
		if(!e.target.parentElement.id){
			newElement.id = "background"
		}

		setElement(newElement)
	}

	return (
		<div className='main'>
			<ColorPanel element = {element}/>
			<Interface className='interface' onClick={(e) => getElement(e)}/>
		</div>
	)
}

export default App;
