import './ColorList.css'

function ColorList ({colors = [], button = ''}) {

    if(button) colors = [...colors, '+', '-']

    return (
        <div className='colors-container'>
            <div className="colors-box">
                {colors.map((color, i) => {
                    if(button && i >= colors.length-2) {
                        return(
                            <div key={i} className="color-container">
                                <div className="color-block" title={color === '+' ? 'addColor' : 'removeColor'} style={{background: 'white'}}>
                                    {color}
                                </div>
                            </div>
                        )
                    }
                    
                    return(
                        <div key={i} className="color-container">
                            <div className="color-block" title={color} style={{background: color}}>
                            </div>
                        </div>
                    )
                })}

            </div>
        </div>
    )
}

export {ColorList}